.landing {
	display: flex;
	align-items: center;
	justify-content: center;
	height: 100vh;
}
.landing h1 {
	font-size: 3rem;
}
.landing_container {
	background: linear-gradient(113.35deg, #1870ed 0%, #333399 100%);
	box-shadow: 0px 0px 40px rgba(0, 0, 0, 0.25);
	display: flex;
	align-items: center;
	flex-direction: column;
	justify-content: space-around;
	width: 60vw;
	height: 60vh;
	padding: 20px;
}

.options {
	display: flex;
	width: 100%;
	justify-content: space-evenly;
}
.play-btn {
	width: 130px;
	height: 130px;
	border: 15px solid #fff;
	border-radius: 100%;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 2rem;
	transition: 0.1s all;
}
.play-btn:hover {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

.sudoku-board {
	margin-bottom: 1em;
}
/*cell (wrapper) contains both cell input and candidates div*/
.sudoku-board-cell {
	display: inline-block;
	position: relative;
	border: 1px solid #ddd;
	background: white;
}
/*rules for drawing the black lines dividing houses
			-- specific for board size 9! need diff rules for diff board sizes
		*/
[data-board-size="9"].sudoku-board .sudoku-board-cell:nth-of-type(9n + 1) {
	border-left-width: 2px;
	border-left-color: #808080;
}
[data-board-size="9"].sudoku-board .sudoku-board-cell:nth-of-type(n):nth-of-type(-n + 9) {
	border-top-width: 2px;
	border-top-color: #808080;
}
[data-board-size="9"].sudoku-board .sudoku-board-cell:nth-of-type(3n) {
	border-right-width: 2px;
	border-right-color: #808080;
}
[data-board-size="9"].sudoku-board .sudoku-board-cell:nth-of-type(n + 19):nth-of-type(-n + 27),
[data-board-size="9"].sudoku-board .sudoku-board-cell:nth-of-type(n + 46):nth-of-type(-n + 54),
[data-board-size="9"].sudoku-board .sudoku-board-cell:nth-of-type(n + 73):nth-of-type(-n + 81) {
	border-bottom-width: 2px;
	border-bottom-color: #808080;
}

/* the input for the value of each cell (i.e. 1-9 or empty for standard sudoku*/
.sudoku-board-cell input {
	background: none;
	font-size: 19.2px;
	font-size: 1.2rem;
	text-align: center;
	width: 3em;
	max-width: 100%;
	height: 3em;
	border: 0;
	position: relative;
	z-index: 1; /*on top of candidates*/
}

/*highlight new input in board cell*/
.sudoku-board-cell .highlight-val {
	color: #00d;
}
/*highligt incorrect input*/
.sudoku-board-cell .board-cell--error {
	background: #d00;
	color: #eee;
}
/*wrapper that hold the candidates for each cell*/
.sudoku-board-cell .candidates {
	display: none; /*hidden by default*/
	position: absolute;
	top: 0;
	bottom: 0;
	left: 0;
	right: 0;
	text-align: center;
	font-size: 0.75em; /*1/3 /1.13 out of the 2*1.2em cell height*/
	letter-spacing: -0.5px;
	font-family: monospace, sans-serif;

	/*use as justify grid*/
	line-height: 0;
	text-align: justify;
}
.sudoku-board .candidates:after {
	content: "";
	display: inline-block;
	width: 100%;
}
/* when board cells start to get narrower because of small screen,
			reduce letter spacing for candidates to make them still fit*/
@media (max-width: 23em) {
	.sudoku-board .candidates {
		letter-spacing: -2px;
	}
}
.showCandidates .candidates {
	display: block;
}

/*each candidate in each cell is wrapped in a div*/
.sudoku-board .candidates div {
	display: inline-block;
	width: 20%;
	line-height: 1.13;
	vertical-align: top;
	*display: inline;
	*zoom: 1;
}
/*.candidates div*/
.candidate--highlight {
	background-color: yellow;
}
/*.candidates div*/
.candidate--to-remove {
	background-color: red;
	color: white;
}
.sudoku-board-cell input {
	padding: 20px;
}
.sudoku-board-cell input:focus {
	background-color: #635fd569;
}

.game {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
}
.game h3 {
	color: #fff;
	font-size: 1.5rem;
}
.timer {
	display: flex;
}
.stop-watch {
	margin: 0px 10px;
}
.timer .pausebtn {
	margin-top: -3px;
}
.su {
	position: relative;
}
.play {
	z-index: 99999;
	position: absolute;
	top: 0;
	left: 0;
	color: #000;
	background-color: #fff;
	height: 100%;
	width: 100%;
}
.play .fa-play-circle {
	position: absolute;
	font-size: 5rem;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	top: 50%;
	left: 50%;
}

.buttons button {
	margin: 0px 20px;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
a {
	color: inherit;
}
a:hover {
	color: inherit;
	text-decoration: none;
}
.App {
	min-height: 100vh;
	color: #fff;
	background: linear-gradient(104.77deg, #ffffff 0%, #f18f88 0.01%, #1870ed 100%);
}

.nes-select {
	width: auto;
}
#dark_select {
	background-color: transparent;

	border: 2px solid #ffffff;
	outline: none;
	width: auto;
}
#dark_select option {
	color: #000;
}
.instructionBtn {
	position: fixed;
	font-size: 0.8rem;
	bottom: 10px;
	right: 10px;
	height: 50px;
	width: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: rgb(40, 44, 44);
	border-radius: 100%;
}
.instruction {
	z-index: 99999;
	padding: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	color: #000;
	-webkit-transform: translate(-50%, -50%);
	        transform: translate(-50%, -50%);
	position: absolute;
	font-size: 0.8rem;
	top: 50%;
	left: 50%;
	height: 80vh;
	width: 80vw;
	background-color: #fff;
}
.instruction .close {
	position: absolute;
	top: 0px;
	right: 10px;
	font-size: 2rem;
}
.instruction img {
	width: 90%;
}

